export const Main = () => {
  document.addEventListener('scroll', () => {
    if(document.querySelector('#header')){
    if (window.scrollY > 100) {
      document.querySelector('#header').classList.add('shadowed')
    }
    else {
      document.querySelector('#header').classList.remove('shadowed');
    }
  }
    if(document.querySelector('.invoices-div nav')){
    if(window.scrollY > document.querySelector('.invoices-div nav').offsetTop) {
        document.querySelector('.invoices-div .h').classList.add('h1-div-card')
        document.querySelector('.invoices-div .navbar').classList.add('navbar-stick')
        if(window.scrollY > document.querySelector('.invoices-div nav').offsetTop) {
          // #article-1
        }
    }else{
      document.querySelector('.invoices-div .h').classList.remove('h1-div-card')
      document.querySelector('.invoices-div .navbar').classList.remove('navbar-stick')
    }
  }
  });
}; 