<script>
import { checkEmail } from '../../assets/js';

export default {
  data: () => {
    return {
      name: '',
      phone: '',
      email: '',
      password: '',
      cPassword: '',
    };
  },
  mounted() {
    document
      .querySelector('.fxt-checkbox-area label')
      .addEventListener('click', (e) => {
        e.target.classList.toggle('checkMe');
        document.querySelector('#tc').checked =
          !document.querySelector('#tc').checked;
      });
  },
  methods: {
    togglePass: (inp, inpCheck) => {
      console.log(inp, inpCheck);
      document.querySelector(inp).type =
        document.querySelector(inp).type === 'password' ? 'text' : 'password';
      document.querySelector(inpCheck).classList.toggle('fa-eye-slash');
      document.querySelector(inpCheck).classList.toggle('fa-eye');
    },
    SignUp: function () {
      //check all fields are filled
      if (
        this.email == '' ||
        this.password == '' ||
        this.cPassword == '' ||
        this.name == ''
      ) {
        alert('Please fill out all fields');
        return false;
      }
      //check email is valid
      if (!checkEmail(this.email)) {
        alert('Please enter valid email');
        return false;
      }
      //check password
      if (this.password != this.cPassword) {
        alert("passwords doesn't match");
        return false;
      }

      //call fetch()  to signup
      fetch('http://tradeopedia.co/api/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: this.name,
          email: this.email,
          phone: this.phone,
          password: this.password,
        }),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.errorMsg == 'Success') {
            localStorage.setItem('userId', data.data.userId);
            localStorage.setItem('name', data.data.name);
            localStorage.setItem('user_email', data.data.email);
            localStorage.setItem('user_phone', data.data.phone);
            localStorage.setItem('expire_date', data.data.expire_date);
            localStorage.setItem('login_time', data.data.login_time);
            localStorage.setItem('packages_id', data.data.packages_id);
            localStorage.setItem('token', data.data.token);
            localStorage.setItem('package_plan', data.data.package_plan);
            localStorage.setItem('device_token', data.data.device_token);
            localStorage.setItem('device_type', data.data.device_type);
            localStorage.setItem(
              'quotation_footer',
              data.data.quotation_footer,
            );
            localStorage.setItem('company_id', data.data.company_id);
            localStorage.setItem('company_logo', data.data.company_logo);
            localStorage.setItem('company_name', data.data.company_name);
            localStorage.setItem('company_gstin', data.data.company_gstin);
            localStorage.setItem(
              'gst_composite_scheme',
              data.data.gst_composite_scheme,
            );
            localStorage.setItem('company_address', data.data.company_address);
            localStorage.setItem(
              'inventory_status',
              data.data.inventory_status,
            );

            if (data.data.company_id == '') {
              this.$router.push('/website_first_profile');
              return false;
            }
            this.$router.push('/website_dashboard');
            return false;
          } else {
            alert(data.errorMsg);
          }
        })
        .catch((error) => {
          console.error('Error:', error);
        });
    },
  },
};
</script>

